// ** React Imports
import { useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** Store & Actions
import { getAutoService } from '@store/AutoServiceRequest/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Reactstrap
import { Alert, Col, Row } from 'reactstrap'

// ** AutoServiceRequest View Components
import InfoCard from './InfoCard'
import PersonalInfoCard from './PersonalInfoCard'
import ProcessOrder from './ProcessOrder'

// ** Custom Components
import UILoader from '@components/ui-loader'
import CarhootBreadCrumbs from '@components/breadcrumbs/CarhootBreadCrumbs'

// ** Styles
import '@styles/react/apps/app-users.scss'

const AutoServiceRequestView = () => {
  // ** Vars
  const store = useSelector(state => state.autoServices),
    dispatch = useDispatch(),
    { id } = useParams()

  const { isLoading } = store
  // ** Get user on mount
  useEffect(() => {
    dispatch(getAutoService(id))
  }, [dispatch])

  return store.selectedData !== null && store.selectedData !== undefined ? (
    <UILoader blocking={isLoading}>
      <CarhootBreadCrumbs
        breadCrumbTitle='Breadcrumbs'
        breadCrumbItems={
          [
            {
              link: `/autoServiceRequest/list`,
              title: 'AutoService Request'
            },
            {
              title: 'AutoService Request Detail'
            }
          ]
        }
      />
      <div className='app-user-view'>
        <Row>
          <Col xl='12'>
            <InfoCard selectedData={store.selectedData} />
          </Col>
        </Row>
        <Row>
          <Col xl='12' lg='12' md='12'>
            <PersonalInfoCard selectedData={store.selectedData} />
          </Col>
        </Row>
        <Row>
          <Col xl='12' lg='12' md='12'>
            <ProcessOrder selectedData={store.selectedData} />
          </Col>
        </Row>
      </div>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>AutoService request not found</h4>
      <div className='alert-body'>
        AutoService request with id: {id} doesn't exist. Check list of all autoService requests: <Link to='/autoServiceRequest/list'>AutoService Request List</Link>
      </div>
    </Alert>
  )
}

export default AutoServiceRequestView
