/*
 *
 * AutoServiceRequest actions
 *
 */

import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  RESET_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET,
  ADD,
  DELETE,
  SET_LOADING,
  SET_SUBMITTING,
  SET_FORM_ERRORS,
  FORM_CHANGE,
  EDIT_CHANGE,
  DEACTIVATE
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const autoServiceFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: FORM_CHANGE,
    payload: formData
  }
}

export const autoServiceEditChange = (name, value, type) => {
  const formData = {}
  if (type === 'option') {
    formData[name] = value.value
  } else {
    formData[name] = value
  }

  return {
    type: EDIT_CHANGE,
    payload: formData
  }
}

// ** Get all Data
export const getAllAutoServices = () => {
  return async dispatch => {
    await axios.get('/api/autoServices').then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.autoServices
      })
    })
  }
}

// ** Get data on page or row change
export const getAutoServiceList = params => {
  return async dispatch => {
    await axios.get('/api/autoServices', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.autoServices,
        totalPages: response.data.total,
        params
      })
    })
  }
}

// ** Get AutoServiceRequest
export const getAutoService = id => {
  return async dispatch => {
    await axios
      .get(`/api/autoServices/${id}`)
      .then(response => {
        dispatch({
          type: GET,
          selectedData: response.data.autoService
        })
      })
      .catch(err => console.log(err))
  }
}

// ** Add autoService
export const addAutoService = () => {
  return async (dispatch, getState) => {
    const rules = {
      userId: 'required',
      userCarId: 'required',
      orderCategoryId: 'required',
      orderPrice: 'required'
    }

    const autoService = getState().autoServices.formData
    const autoServiceData = {
      userId: autoService.userId ? autoService.userId.value : undefined,
      userCarId: autoService.userCarId ? autoService.userCarId.value : undefined,
      carhootEmployeeId: autoService.carhootEmployeeId ? autoService.carhootEmployeeId.value : undefined,
      fieldAgentId: autoService.fieldAgentId ? autoService.fieldAgentId.value : undefined,
      vendorId: autoService.vendorId ? autoService.vendorId.value : undefined,
      vendorEmployeeId: autoService.vendorEmployeeId ? autoService.vendorEmployeeId.value : undefined,
      orderCategoryId: autoService.orderCategoryId ? autoService.orderCategoryId.value : undefined,
      orderPrice: autoService.orderPrice
    }

    const { isValid, errors } = allFieldsValidation(autoServiceData, rules, {
      'required.userId': 'User is required.',
      'required.userCarId': 'User car is required.',
      'required.orderCategoryId': 'Order category is required.',
      'required.orderPrice': 'Order price is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_SUBMITTING, payload: true })
    dispatch({ type: SET_LOADING, payload: true })

    try {
      const response = await axios.post('/api/autoServices', autoServiceData)
      dispatch({
        type: ADD,
        autoServiceData
      })
      dispatch({
        type: RESET_FORM,
        payload: {}
      })
      dispatch(getAutoServiceList(getState().autoServices.params))
      dispatch(getAllAutoServices())

      const successfulOptions = {
        title: `The autoService has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the autoService. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete autoService
export const updateAutoService = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      userId: 'required',
      userCarId: 'required',
      orderCategoryId: 'required',
      orderPrice: 'required'
    }

    const autoService = { ...getState().autoServices.selectedData }
    const { isValid, errors } = allFieldsValidation(autoService, rules, {
      'required.userId': 'User is required.',
      'required.userCarId': 'User car is required.',
      'required.orderCategoryId': 'Order category is required.',
      'required.orderPrice': 'Order price is required.'
    })


    if (!isValid) {
      return dispatch({ type: SET_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_SUBMITTING, payload: true })
    dispatch({ type: SET_LOADING, payload: true })

    try {
      delete autoService._id
      delete autoService.categoryInfo
      delete autoService.employeeInfo
      delete autoService.fieldAgentInfo
      delete autoService.userCarInfo
      delete autoService.userInfo
      delete autoService.vendorEmployeeInfo
      delete autoService.vendorInfo
      delete autoService.createdAt
      delete autoService.updatedAt
      delete autoService.__v
      const response = await axios.put(`/api/autoServices/${_id}`, autoService)
      dispatch({
        type: RESET_FORM,
        payload: {}
      })

      const successfulOptions = {
        title: `AutoService information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the autoService. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete autoService
export const deactivateAutoService = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/autoServices/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the autoService. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete autoService
export const deleteAutoService = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.delete(`/api/autoServices/${id}`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DELETE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the autoService. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}
