// ** React Imports
import { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

// ** User Edit Components
import DeliveryTeam from './OrderProcessing/AssignDeliveryTeam'
import OrderDetails from './OrderProcessing/OrderDetails'
import OrderDelivery from './OrderProcessing/OrderDelivery'
import OrderPayment from './OrderProcessing/OrderPayment'
import CloseOrder from './OrderProcessing/CloseOrder'

// ** Store & Actions
import { getUser } from '@store/Users/actions'
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import { Info, Share2, User } from 'react-feather'
import { Alert, Card, CardBody, Col, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap'

// ** Custom Components
import UILoader from '@components/ui-loader'

// ** Styles
import '@styles/react/apps/app-users.scss'

const UserEdit = () => {
  // ** States & Vars
  const [activeTab, setActiveTab] = useState('1'),
    dispatch = useDispatch(),
    { isLoading, selectedUser } = useSelector(state => state.users),
    { id } = useParams()

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

  // ** Function to get user on mount
  useEffect(() => {
    dispatch(getUser(id))
  }, [dispatch])

  return (
    <UILoader blocking={isLoading}>
      <Row className='app-user-edit'>
        <Col sm='12'>
          <Card>
            <CardBody className='pt-2'>
              <Nav pills>
                <NavItem>
                  <NavLink active={activeTab === '1'} onClick={() => toggle('1')}>
                    <User size={14} />
                    <span className='align-middle d-none d-sm-block'>Delivery Team</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={activeTab === '2'} onClick={() => toggle('2')}>
                    <Info size={14} />
                    <span className='align-middle d-none d-sm-block'>Order Details</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={activeTab === '3'} onClick={() => toggle('3')}>
                    <Share2 size={14} />
                    <span className='align-middle d-none d-sm-block'>Order Delivery</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={activeTab === '4'} onClick={() => toggle('4')}>
                    <Share2 size={14} />
                    <span className='align-middle d-none d-sm-block'>Order Payment</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={activeTab === '5'} onClick={() => toggle('5')}>
                    <Share2 size={14} />
                    <span className='align-middle d-none d-sm-block'>Close Order</span>
                  </NavLink>
                </NavItem>
              </Nav>
              <TabContent activeTab={activeTab}>
                <TabPane tabId='1'>
                  <DeliveryTeam selectedUser={selectedUser} />
                </TabPane>
                <TabPane tabId='2'>
                  <OrderDetails />
                </TabPane>
                <TabPane tabId='3'>
                  <OrderDelivery />
                </TabPane>
                <TabPane tabId='4'>
                  <OrderPayment />
                </TabPane>
                <TabPane tabId='5'>
                  <CloseOrder />
                </TabPane>
              </TabContent>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </UILoader>
  )
}

export default UserEdit
